var sound2 = new THREE.PositionalAudio( listener4 );
Tone.setContext(sound2.context);
let oscillator2 = new Tone.Oscillator(220, "sine").start().toDestination();
sound2.setNodeSource (oscillator2);
sound2.setRefDistance( 100 );
sound2.setDirectionalCone( 180, 230, 0.9 );
