mesh3.parent = camera


camera.zoom = 4



let spotcolor = new THREE.Color(255, 0, 0)
var spotLight = new THREE.SpotLight(spotcolor);
spotLight.position.set(0, 0, -100);

spotLight.castShadow = true;

