controls.update();
controls.minDistance = 0.1;
controls.maxDistance = 0.1;
controls.minZoom = 0.1
controls.enableZoom = false;
controls.maxPolarAngle = 0.5 * Math.PI;





document.body.appendChild( VRButton.createButton( renderer ) );

renderer.xr.enabled = true;

renderer.setAnimationLoop( function () {

   renderer.render( scene, camera );
   Tone.start()

} );




setInterval(randomizePartials, 10);

function randomizePartials(){
  let partials = [];
  for(let i = 0; i < x; i++){
    partials.push((i / x) + 0.00000001 );
  }
  oscillator1.partials = partials;
  oscillator2.partials = partials;
  oscillator3.partials = partials;
  oscillator4.partials = partials;
//   console.log(partials)
}


/**
 * Animate
 */




const tick = () => {
   //  let frequencyData = analyzer.getValue();


   x += speed

   if (x > 6.5 || x < -0.2) {
      speed = speed * -1
   }

   // mesh1.position.x = x * 10
   // mesh1.position.z = x * 10

   // mesh2.position.x = -x * 20
   // mesh2.position.z = -x * 20

   sloop2 = new THREE.Color(0.2 + Math.sin(x / 40), 0.2 + Math.sin(x / 24), 0.2 + Math.sin(x / 16));  // white
   sloop1 = new THREE.Color(0.2 + Math.sin(x / 20), 0.2 + Math.sin(x / 12), 0.2 + Math.sin(x / 8));  // white
   light1 = new THREE.Color(0.2 + Math.sin(x / 2), 0.2 + Math.sin(x / 4), 0.2 + Math.sin(x));
   color = new THREE.Color(0.2 + Math.sin(x / 10), 0.2 + Math.sin(x / 8), 0.2 + Math.sin(x / 3));
   const near = 10 + (x * 10)
   const far = 20 + (x * 10);
   scene.fog = new THREE.Fog(light1, near, far);

   scene.background = new THREE.Color(0.2 + Math.sin(x / 5), 0.2 + Math.sin(x / 3), 0.2 + Math.sin(x / 2));

   // Update controls
   controls.update()

   // Render
   renderer.render(scene, camera)

   // Call tick again on the next frame

   window.requestAnimationFrame(tick)


}


tick()